<p className="card-text">Considerado um dos melhores aplicativos para o aprendizado de LIBRAS, o Hand Talk faz uma tradução automática de texto e voz para a linguagem de sinais. O app é bastante interativo e conta com a ajuda de um intérprete 3D, que também comanda a apresentação de vídeos que ensinam expressões e sinais em LIBRAS. Tanto adultos quanto crianças podem usar este app. </p>

