import React from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';

class ModalExample extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      modal: true
    };

    this.toggle = this.toggle.bind(this);
  }

  toggle() {
    this.setState(prevState => ({
      modal: !prevState.modal
    }));
  }

  render() {
    return (
      <div>
       
        <Modal isOpen={this.state.modal} toggle={this.toggle} className={this.props.className}>
          <ModalHeader toggle={this.toggle}>26 de Setembro - Dia Nacional do Surdo</ModalHeader>
          <ModalBody>
          <p>A pessoa surda é aquela que possui surdez completa, que geralmente é uma característica de nascença.  </p>
O surdo possui costumes e uma história. Tendo isso em vista, o setembro azul busca valorizar e divulgar a cultura surda.  </ModalBody>
          <ModalFooter>
          
            <Button color="secondary" onClick={this.toggle}>Fechar</Button>
          </ModalFooter>
        </Modal>
      </div>
    );
  }
}

export default ModalExample;